enum Fields {
  TOKEN0 = 0,
  TOKEN1 = 1,
}

function useQuery() {
  return new URLSearchParams(useLocation().search);
}

function toWei(num: any) {
  return web3.utils.toWei(Number(num).toFixed(18), "ether");
}

function toEther(num: any) {
  return web3.utils.fromWei(Number(num).toString(16), "ether");
}

const menu = (
  <Menu>
    <Menu.Item>
      <a>ETH-USDT</a>
    </Menu.Item>
  </Menu>
);

export default function GridTrading() {
  const { account, chainId, library } = useActiveWeb3React();
  const outContract = useContract(contractAddr, OutAbi);
  const ercContract = useContract(dai_ropsten_address, ErcAbi);

  const [loading, setLoading] = useState(false);
  const [tickCount, setTickCount] = useState(0);
  const [amount, setAmount] = useState(0);
  const [tickMin, setTickMin] = useState(0);
  const [tickMax, setTickMax] = useState(0);

  async function pullStrategies() {
    if (outContract) {
      const ids = await outContract.getUserInfo(account);
      console.log("ids", ids);
      const strategies = await outContract.getAllStrategy(ids);
      console.log(strategies);
    }
  }

  async function submitStrategy() {
    setLoading(true);
    try {
      console.log("tickCount", tickCount);
      console.log("amount", amount);
      console.log("tickMin", tickMin);
      console.log("tickMax", tickMax);
      if (tickCount <= 0) {
        message.error("请输入网格数量");
        return;
      }
      if (amount <= 0) {
        message.error("请输入资金数量");
        return;
      }
      if (tickMin <= 0) {
        message.error("请输入最低价");
        return;
      }
      if (tickMax <= 0) {
        message.error("请输入最高价");
        return;
      }
      if (tickMax <= tickMin) {
        message.error("最高价应大于最低价");
        return;
      }
      console.log("submitStrategy");
      await pullStrategies();
      if (ercContract && outContract && library) {
        const allowance: any = await ercContract.allowance(
          account,
          contractAddr
        );
        console.log("the allowance is ", toEther(allowance));
        // TODO 修改判断条件
        if (Number(toEther(allowance)) < amount) {
          console.log("need to approve");
          const response: TransactionResponse = await ercContract.approve(
            contractAddr,
            toWei(amount),
            {
              gasLimit: 300000,
            }
          );
          console.log("approve:response", response);
          const receipt = await waitTransaction(library, response.hash);
          console.log("approve:receipt", receipt);
          const success = isSuccessfulTransaction(receipt);
          console.log("approve:success", success);
          if (!success) {
            message.error("未成功授权USDT, 无法进行后续操作");
            return;
          }
        }
        console.log("create");
        const response: TransactionResponse = await outContract.create(
          dai_ropsten_address,
          weth_ropsten_address,
          500,
          toWei(amount),
          account,
          toWei(tickMin),
          toWei(tickMax),
          Math.round(tickCount),
          {
            gasLimit: 300000,
          }
        );
        const receipt = await waitTransaction(library, response.hash);
        console.log("receipt", receipt);
        const success = isSuccessfulTransaction(receipt);
        if (!success) {
          message.error("未成功创建策略");
          return;
        } else {
          message.success("成功创建策略");
          return;
        }
      }
    } catch (err) {
    } finally {
      setLoading(false);
    }
  }

  const MarketPanel = (
    <MarketPanelWrapper>
      <TradingViewWidget
        symbol="BINANCE:ETHUSDT"
        theme={Themes.LIGHT}
        autosize
      />
    </MarketPanelWrapper>
  );

  const OrderPanel = (
    <OrdersOperatePanelWrapper>
      <RowItem>
        <RowFixed>
          <TYPE.black
            fontWeight={500}
            fontSize={16}
            style={{ marginRight: "8px" }}
          >
            网格交易
          </TYPE.black>
        </RowFixed>
      </RowItem>

      <RowItem>
        <div className="card">
          <RowItem>
            <Text>币对</Text>
            <Dropdown overlay={menu} placement="bottomLeft">
              <Button>ETH-USDT</Button>
            </Dropdown>
          </RowItem>
          <RowItem>
            <InputNumber
              min={0}
              placeholder={"最低价"}
              onChange={(v) => setTickMin(v)}
              value={tickMin != 0 ? tickMin : undefined}
            />
            <InputNumber
              min={0}
              placeholder={"最高价"}
              onChange={(v) => setTickMax(v)}
              value={tickMax != 0 ? tickMax : undefined}
            />
          </RowItem>
          <RowItem>
            <Radio.Group>
              <Radio.Button value="large">等差</Radio.Button>
              <Radio.Button value="default">等比</Radio.Button>
            </Radio.Group>
            <InputNumber
              min={0}
              placeholder={"网格数量"}
              onChange={(v) => setTickCount(v)}
              value={tickCount != 0 ? tickCount : undefined}
            />
          </RowItem>
          <RowItem>
            <InputNumber
              style={{ width: "100%" }}
              min={0}
              placeholder={"输入资金数量(USDT)"}
              onChange={(v) => setAmount(v)}
              value={amount != 0 ? amount : undefined}
            />
          </RowItem>
        </div>
      </RowItem>

      <RowItem>
        <RowFixed>
          <TYPE.black
            fontWeight={500}
            fontSize={16}
            style={{ marginRight: "8px" }}
          >
            高级设置
          </TYPE.black>
        </RowFixed>
      </RowItem>

      <div className="card">
        <RowItem2>
          <Text>自动止损</Text>
          <InputNumber
            min={0}
            placeholder={"选填(USDT)"}
            onChange={(v) => console.log(v)}
          />
        </RowItem2>
      </div>

      <RowItem2>
        <Button
          loading={loading}
          className="create-btn"
          onClick={() => submitStrategy()}
        >
          存入并创建
        </Button>
      </RowItem2>
    </OrdersOperatePanelWrapper>
  );

  const orders = (
    <OrdersPanelWrapper>
      <RowItem>
        <RowFixed>
          <TYPE.black
            fontWeight={500}
            fontSize={16}
            style={{ marginRight: "8px" }}
          >
            机器人订单
          </TYPE.black>
        </RowFixed>
      </RowItem>
      <OrderTitle>
        <OrderInfoLeft>{"机器人类别"}</OrderInfoLeft>
        <OrderInfoMid>{"交易对"}</OrderInfoMid>
        <OrderInfoMid>{"投资额"}</OrderInfoMid>
        <OrderInfoMid>{"利润"}</OrderInfoMid>
        <OrderInfoRight>{"操作"}</OrderInfoRight>
      </OrderTitle>
      <OrderList>
        <OrderContentList>
          <OrderItem>
            <OrderInfoLeft>{"网格机器人"}</OrderInfoLeft>
            <OrderInfoMid>{"ETH-USDT"}</OrderInfoMid>
            <OrderInfoMid>{"500USDT"}</OrderInfoMid>
            <OrderInfoMid>{"100USDT"}</OrderInfoMid>
            <OrderInfoRight>
              <Button className="stop-btn">停止并取出</Button>
            </OrderInfoRight>
          </OrderItem>
        </OrderContentList>
      </OrderList>
    </OrdersPanelWrapper>
  );

  return (
    <>
      <PageWrapper>
        <PageWrapperUp>
          {MarketPanel}
          {OrderPanel}
        </PageWrapperUp>

        {orders}
      </PageWrapper>

      <SwitchLocaleLink />
    </>
  );
}
